.hero {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    &__body {
        width: 50%;
    }

    &__title {
        margin-bottom: 20px;
    }

    &__description {
        margin-bottom: 35px;
        
    }

    &__button {
        position: relative;
        margin-bottom: 60px;
        padding-right: 55px !important;
    }

    &__link::after {
        content: "";
        position: absolute;
        display: block;
        right: 35px;
        top: 27px;
        background-image: url("../../img/top-right-arrow.svg");
        width: 16px;
        aspect-ratio: 1;
    }

    &__members {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        height: 70px;
        justify-content: center;

        &-title {
            width: 77%;
        }
    }

    &__image {
        width: 50%;
    }

    &__partners {
        &-list {
            display: flex;
            column-gap: 64px;
        }
    }
}